.slider input[type="radio"] {
  display: none;
}

.slider-controls {
  text-align: center;
  order: 2;
  margin-top: 15px;

  @media (min-width: @mobile-width-middle) {
    margin-top: 14px;
    padding-left: 15px;
  }
}

.slider-controls label {
  display: inline-block;
  margin-right: 16px;
  width: 12px;
  height: 12px;
  cursor: pointer;
  background-color: @light-grey;
  border-radius: 50%;
  transition: background-color 0.2s;
}

.slider-controls label:last-child {
  margin-right: 0;
}

label[for="btn-1"],
label[for="btn-2"],
label[for="btn-3"],
label[for="btn-4"],
label[for="btn-5"] {
  @media (min-width: @tablet-width) {
    display: none;
  }
}

label[for="btn-6"],
label[for="btn-7"],
label[for="btn-8"],
label[for="btn-9"],
label[for="btn-10"] {
  @media (min-width: @mobile-width-middle) {
    display: none;
  }
}

.radio1:checked ~ .slider-controls label[for="btn-1"],
.radio2:checked ~ .slider-controls label[for="btn-2"],
.radio3:checked ~ .slider-controls label[for="btn-3"],
.radio4:checked ~ .slider-controls label[for="btn-4"],
.radio5:checked ~ .slider-controls label[for="btn-5"],
.radio6:checked ~ .slider-controls label[for="btn-6"],
.radio7:checked ~ .slider-controls label[for="btn-7"],
.radio8:checked ~ .slider-controls label[for="btn-8"],
.radio9:checked ~ .slider-controls label[for="btn-9"],
.radio10:checked ~ .slider-controls label[for="btn-10"] {
  background-color: @gold;
}

.radio1:checked ~ .slider-inner .slider-slides {
  transform: translate(0);
}

.radio2:checked ~ .slider-inner .slider-slides {
  transform: translate(-300px);

  @media (min-width: @mobile-width-middle) {
    transform: translate(-580px);
  }
}

.radio3:checked ~ .slider-inner .slider-slides {
  transform: translate(-300px*2);

  @media (min-width: @mobile-width-middle) {
    transform: translate(-580px*2);
  }
}

.radio4:checked ~ .slider-inner .slider-slides {
  transform: translate(-300px*3);

  @media (min-width: @mobile-width-middle) {
    transform: translate(-580px*3);
  }
}

.radio5:checked ~ .slider-inner .slider-slides {
  transform: translate(-300px*4);

  @media (min-width: @mobile-width-middle) {
    transform: translate(-580px*4);
  }
}

.radio6:checked ~ .slider-inner .slider-slides {
  transform: translate(-300px*5);
}

.radio7:checked ~ .slider-inner .slider-slides {
  transform: translate(-300px*6);
}

.radio8:checked ~ .slider-inner .slider-slides {
  transform: translate(-300px*7);
}

.radio9:checked ~ .slider-inner .slider-slides {
  transform: translate(-300px*8);
}

.radio10:checked ~ .slider-inner .slider-slides {
  transform: translate(-300px*9);
}

.slider-arrow-right,
.slider-arrow-left {
  display: none;
  width: 0;
  height: 0;

  @media (min-width: @tablet-width) {
    display: inline-block;
    position: absolute;
    content: "";
    width: 12px;
    height: 12px;
    box-sizing: border-box;
    border-left: 2px solid @gold;
    border-bottom: 2px solid @gold;
    top: 194px;
    cursor: pointer;
  }

  @media (min-width: @desktop-width) {
    width: 25px;
    height: 25px;
    top: 257px;
  }
}

.slider-arrow-left {
  transform: rotate(45deg);
  left: -22px;

  @media (min-width: @desktop-width) {
    left: -44px;
  }
}

.slider-arrow-right {
  transform: rotate(-135deg);
  right: -22px;

  @media (min-width: @desktop-width) {
    right: -44px;
  }
}

.slider-arrow-left:active,
.slider-arrow-right:active {
  opacity: 0.2;
  border-width: 1px;
}

.slider-arrow-left:focus,
.slider-arrow-right:focus,
.slider-arrow-left:hover,
.slider-arrow-right:hover {
  opacity: 0.6;
}

