.buttons {
  margin-top: 14px;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 640px;
    margin: 25px auto 0;
  }

  @media (min-width: @desktop-width) {
    width: 760px;
  }
}

.button-expert {
  border: 1px solid @gold;
  padding-right: 0;
  padding-left: 0;

  @media (min-width: @tablet-width) {
    width: 300px;
    padding: 10px 0 8px;
  }

  @media (min-width: @desktop-width) {
    width: 360px;
    padding-top: 18px;
  }
}

.button-expert:hover,
.button-expert:focus {
  position: relative;
  text-align: left;
  padding-left: 2px;

  @media (min-width: @mobile-width-middle) {
    padding-right: 35px;
    text-align: center;
    padding-left: 0;
  }

  @media (min-width: @tablet-width) {
    padding-right: 30px;
  }

  &::after {
    position: absolute;
    content: "";
    background-image: url("../img/arrow-right.svg");
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
    fill: @gold;
    width: 23px;
    height: 23px;
    top: 9px;
    right: 0;

    @media (min-width: @mobile-width-middle) {
      right: 130px;
    }

    @media (min-width: @tablet-width) {
      right: 0;
      top: 11px;
    }

    @media (min-width: @desktop-width) {
      top: 18.5px;
      right: 20px;
    }
  }
}

.button-project {
  color: @white;
  background-color: @gold;
  padding: 11px;
  text-align: center;
  padding-right: 0;
  padding-left: 0;
  margin-top: 20px;

  @media (min-width: @tablet-width) {
    margin-top: 0;
    width: 300px;
    padding: 11px 0 9px;
  }

  @media (min-width: @desktop-width) {
    width: 360px;
    padding: 18.5px;
  }
}

.button-project:active {
  color: fade(@white, 80%);
  opacity: 0.8;
}

.button-project:hover,
.button-project:focus {
  position: relative;
  padding-right: 45px;
  padding-left: 11px;

  &::after {
    position: absolute;
    content: "\2192";
    color: @white;
    width: 25px;
    height: 23px;
    top: 10px;
    right: 10px;

    @media (min-width: @mobile-width-middle) {
      right: 140px;
    }

    @media (min-width: @tablet-width) {
      right: 10px;
    }

    @media (min-width: @desktop-width) {
      right: 25px;
      top: 15px;
    }
  }
}
