.cards {
  margin: 0 auto;
  margin-top: 16px;
  width: 300px;

  @media (min-width: @mobile-width-middle) {
    width: 560px;
    margin-top: 18px;
  }

  @media (min-width: @tablet-width) {
    width: 1200px;
    margin-top: 37px;
  }

  @media (min-width: @desktop-width) {
    width: 1760px;
  }
}

.slider {
  position: relative;
  display: flex;
  flex-direction: column;
}

.slider-inner {
  overflow: hidden;
}

.slider-slides {
  width: 400%;
  transition: transform 0.8s ease;

  @media (min-width: @mobile-width-middle) {
    width: 200%;
  }
}

.slider-slides picture {
  float: left;
  width: 300px;
  height: 360px;

  @media (min-width: @mobile-width-middle) {
    width: 270px;
  }

  @media (min-width: @desktop-width) {
    width: 410px;
    height: 540px;
  }
}

.slider-slides img {
  @media (min-width: @mobile-width) {
    height: 360px;
  }

  @media (min-width: @desktop-width) {
    height: 540px;
  }
}

.slider-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: no-wrap;
  justify-content: space-between;

  @media (min-width: @mobile-width-middle) {
    justify-content: start;
  }
}

.slider-item {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  width: 25%;

  @media (min-width: @mobile-width-middle) {
    width: 270px;
    margin-right: 20px;
  }

  @media (min-width: @tablet-width) {
    margin-right: 40px;
  }

  @media (min-width: @desktop-width) {
    width: 410px;
  }
}

.cards__link {
  display: flex;
  flex-direction: column;
}

.cards__link:active {
  opacity: 0.6;
}

.cards__link:hover .cards__subtitle,
.cards__link:focus .cards__subtitle {
  opacity: 0.8;
}

.cards__subtitle {
  margin: 0;
  margin-top: 10px;
  font-size: 24px;
  line-height: 1;

  @media (min-width: @mobile-width-middle) {
    margin-top: 10px;
  }

  @media (min-width: @tablet-width) {
    margin-top: 20px;
  }

  @media (min-width: @desktop-width) {
    font-size: 32px;
  }
}

.cards__description {
  margin: 0;
  margin-top: 10px;
  font-size: 16px;

  @media (min-width: @tablet-width) {
    margin-top: 20px;
  }

  @media (min-width: @desktop-width) {
    font-size: 20px;
  }
}
