.page-main {
  padding: 0 10px;
  margin: 0 auto;
  width: 300px;

  @media (min-width: @mobile-width-middle) {
    width: 560px;
    margin-top: 5px;
  }

  @media (min-width: @tablet-width) {
    width: 1200px;
  }

  @media (min-width: @desktop-width) {
    width: 1760px;
    margin-top: 11px;
  }
}

.page-title {
  @media (min-width: @mobile-width-middle) {
    display: flex;
    justify-content: space-between;
  }
}

.page-title__title {
  text-transform: uppercase;
  font-weight: 800;
  font-size: 24px;
  line-height: 24px;
  color: @gold;
  margin: 0;

  @media (min-width: @mobile-width-middle) {
    font-size: 32px;
    letter-spacing: 1.2px;
  }

  @media (min-width: @desktop-width) {
    font-size: 48px;
    letter-spacing: 2px;
  }
}

.page-title__button {
  font-size: 18px;
  padding: 0;
  margin: 0;
  text-align: left;
  margin-top: 10px;
  position: relative;

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: @mobile-width-middle) {
    padding-right: 31px;
    margin-top: 3px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 10px;
  }
}

.page-title__button::after {
  position: absolute;
  content: "";
  background-image: url("../img/arrow-right.svg");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
  fill: @gold;
  width: 23px;
  height: 23px;
  top: 0;
  right: 72px;

  @media (min-width: @mobile-width-middle) {
    right: 0;
  }

  @media (min-width: @desktop-width) {
    top: 10px;
  }
}
