.button {
  box-sizing: border-box;
  padding: 7px;
  color: @gold;
  text-align: center;
  outline: none;
  text-transform: uppercase;
  display: block;
  font-size: 18px;
  border: none;

  &:active {
    color: fade(@gold, 80%);
    opacity: 0.8;
  }

  @media (min-width: @desktop-width) {
    padding: 18.5px;
  }
}
